@import url('https://fonts.cdnfonts.com/css/poppins');
@import url('https://fonts.cdnfonts.com/css/raleway-5');

:root{
    /*PRINCIPAL*/
    --principal-color : #774669;
    --principal-color-text : white;
    --shadow-principal-color : #7746693e;
    --navbar-color : #774669;

    /*SECONDARY*/
    --secondary-color : white;
    --secondary-color-text : black;

    /*TERTIARY*/
    --tertiary-color : #1cabc8;
    --shadow-tertiary-color : #1ea8a8;
    --shadow-webkit-color : #1ea8a8;
    --shadow-moz-color : #1ea8a8;
}

html{
    scroll-behavior: smooth;
}
section[id]{
    scroll-margin-top: 120px;
}
.section-nosotros{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    background: var(--principal-color);
    /* position: relative; */
    color: var(--principal-color-text);
    font-weight: 300;
    margin-top: 1.5rem;
}
