.section-nosotros-container{
    display: flex;
    padding: 0 2rem;
    font-family: 'Poppins', sans-serif; 
    margin: 0 .2rem;
    position: relative;
}
.section-nosotros-img{
    margin: 0 auto;
}
.section-nosotros-left-quienes-somos-text{
    text-align: justify;
    font-size: 1.2em;
    max-width: 60%;
    margin: 0 auto;
}
.section-nosotros-left-caracteristicas{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

}
.caracteristica-img{
    width: 30%;
    margin: 0 auto;
}
.detalle-span{
    border-bottom: solid var(--tertiary-color) .2rem;
    font-weight: bold;
}
.section-nosotros-left-caracteristica{
    background: var(--secondary-color);
    color: var(--secondary-color-text);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 2rem 1rem;
    padding: 1rem;
    border: solid var(--tertiary-color) .1rem;
    text-align: center;
    width: 350px;
    height: 350px;
    font-size: 1.2rem;
    border-radius: 15px;
    box-shadow: 2px 21px 7px -2px var(--shadow-tertiary-color);
    -webkit-box-shadow: 2px 21px 7px -2px var(--shadow-webkit-color);
    -moz-box-shadow: 2px 21px 7px -2px var(--shadow-moz-color);
    position: relative;
}
.section-nosotros-left-caracteristica-detalle{
    text-align: justify;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.wave-nosotros{
    background: var(--secondary-color);
}
.wave-servicios{
    background: var(--secondary-color);
    margin-top: -100px;
}
.servicios-title, .contactanos-title{
    text-shadow: 0  .3rem .4rem var(--secondary-color-text);
    text-align: center;
    margin: 0rem auto;
    margin-top: -150px;
    font-size: 3em;
    color: var(--secondary-color-text);
    width: 50%;
    font-weight: 280;
    text-transform: uppercase;
}
.section-nosotros-left-quienes-somos, .section-tarifas-title{
    color: var(--principal-color-text);
    text-shadow: 0rem  .1rem .1rem var(--principal-color-text);
    text-align: center;
    margin: 2rem auto;
    font-size: 3em;
    width: 50%;
    font-weight: 280;
    text-transform: uppercase;
}

.section-servicios{
    font-family: 'Poppins', sans-serif; 
    background: var(--secondary-color);
    position: relative;
}
.servicio-span{
    text-align: center;
    font-weight: bold;
    color: var(--principal-color-text);
    font-size: 1.3em;
    background: black;
    border-top-right-radius: 30px;
    width: 100%; 
    padding: .2rem .5rem; 
    box-sizing: border-box;
    box-shadow: -20px 16px 13px -10px rgba(125,120,120,1);
    -webkit-box-shadow: -20px 16px 13px -10px rgba(125,120,120,1);
    -moz-box-shadow: -20px 16px 13px -10px rgba(125,120,120,1);  
}
.servicio-text{
    overflow-y: auto;
    text-align: justify;
    width: 100%;
    background: var(--secondary-color);
    height: 140px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    box-sizing: border-box;
    border-bottom-left-radius: 50px;
    box-shadow: -20px 16px 13px -10px rgba(125,120,120,1);
    -webkit-box-shadow: -20px 16px 13px -10px rgba(125,120,120,1);
    -moz-box-shadow: -20px 16px 13px -10px rgba(125,120,120,1);
}

.container-servicios{
    padding: 2rem .2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.servicio-datos{
    /* background: red; */
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: .5rem;
    box-sizing: border-box;
}

.servicio{
    /* background-color: blue; */
    width: 550px;
    height: 500px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    box-sizing: border-box;
    margin: 2rem;
}
.servicio img{
    width: 60%;
    filter: brightness(1.1);
    mix-blend-mode: multiply;
}
.section-tarifas{
    text-align: center;
    background: var(--principal-color);
    padding: 3rem 0;
    color: var(--principal-color-text);
    margin-top: -4px;
    margin-bottom: 100px;
}
.table{
    width: 80%;
    text-align: center;
    font-family: 'Poppins', sans-serif; 
    margin: 0 auto;
}
.vehiculo-columna{
    border-bottom: solid 1px var(--tertiary-color);
    display: flex;
    align-items: center;
    justify-content: start;
    font-size: 1.3em;
    padding: .2rem .5rem;
    font-weight: 100;
}
.precio-columna{
    padding: 0 .5rem;
    font-size: 1.3em;
    border-bottom: solid 1px var(--tertiary-color);
    font-weight: 100;
}
.cabecera-table{
    padding: .5rem;
    font-size: 1.1rem;
    background: var(--tertiary-color) ;
}
.container-icon-map{
    position: absolute;
    right: 10%;
    top: 5%;
    font-size: 1.5em;
    display: flex;
    flex-direction: column;
    color: var(--tertiary-color);
}
.container-icon-map span{
    font-size: .4em;
    color: var(--tertiary-color);
}
.icon-link-map{
    cursor: pointer;
}

.btn-wsp{
    filter: drop-shadow(0 0.125rem 0.313rem rgba(0,0,0,.7));
    position: fixed;
    line-height: 55px;
    bottom: 8%;
    right: 1%;
    border-radius: 50px;
    opacity: .6;
    cursor: pointer;
    transition: all .1s ease;
}
.btn-wsp img{
    width: 4rem;
    height: 4rem;
}
.btn-wsp:hover{
    transform: scale(1.1);
    opacity: 1;
}
.section-contactenos{
    display: flex;
    flex-direction: column;
}
.container-map{
    width: 100%;
    height: 450px;
    margin: 0 auto;
}
.leaflet-container{
    width: 100%;
    height: 100%;
}
.container-contactanos{
    text-align: center;
    font-family: 'Poppins', sans-serif;
    padding: 1.5rem 1rem;
    margin-top: 180px;
}
.contactanos-text{
    font-size: 1.2em;
}
.contactanos-formulario-inputs{
    border-radius: 10px;
    width: 60%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    gap: 1rem;
    padding: 1.5rem;
    align-items: center;
    border-top: solid 2px var(--navbar-color) ;
    border-bottom: solid 2px var(--navbar-color) ;
}
.input-item{
    font-size: 1em;
    width: 70%;
    margin: .5rem 0;
    outline: none;
    height: 3.5rem;
    box-sizing: border-box;
    background: var(--shadow-principal-color);
    border: none;
    padding: 0 1rem;
    color: var(--secondary-color-text);
    font-family: 'Poppins','sans-serif';
}
.formulario-input{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5rem;
    width: 100%;
}
.span-input{
    font-size: 1.5em;
}
.btn-enviar{
    width: 60%;
    height: 4rem;
    font-size: 2em;
    font-family: 'Raleway', sans-serif;
    font-weight: bolder;
    cursor: pointer;
    margin: .5rem;
    border: none;
    background-color: var(--principal-color);;
    color: var(--principal-color-text);
    transition: .2s all ease;
    border-radius: 10px;
    box-shadow: 0 .20rem .25rem black;
}
.btn-enviar:hover{
    background-color: var(--shadow-principal-color);;
}
@media (max-width: 1382px){
    /* .section-carousel{
        width: 100%;
        height: 400px;

    }
    .section-carousel img{
        object-fit: cover;      
    }
    .section-carousel img:hover{
        object-fit: cover;
        cursor: crosshair;
        width: 200px;
        opacity: 1;
        filter: contrast(120%);
    } */
    .wave-servicios{
        margin-top: -60px;
    }
    .wave-nosotros,.wave-tarifas{
        margin-bottom: 150px;
    }
    .btn-wsp img{
        width: 3rem;
        height: 3rem;
    }
    .caracteristica-img{
        width: 12%;
    }
    .section-nosotros-left-quienes-somos-text, .contactanos-text{
        font-size: 1em;
    } 
    .section-nosotros-left-quienes-somos-text{
        margin: 1rem auto;
        max-width: 90%;
    }    
    .contactanos-text{
        padding: 0 1rem;
    }
    .section-nosotros-container{
        max-width: 100%;
        font-size: 1em;
    }    
    .servicio{
        font-size: 1.1em;
        width: 100%;
        margin-bottom: .5rem;
    }
    .servicio img{
        width: 40%;
    }
    .section-nosotros-left-quienes-somos{
        font-size: 2.5em;
    }
    .servicio-text, .servicio-span{
        text-align: center;
        width: 100%;
    }
    .servicio-datos{
        width: 100%;
    }
    .section-nosotros-left-caracteristica{
        font-size: 1.2rem;
        width: 100%;
        padding: .75rem 0;
    }
    .section-nosotros-left-caracteristica-detalle{
        margin: 1.5em;
    }
    .formulario-input{
        width: 90%;
    }
    .contactanos-formulario-inputs{
        width: 80%;
    }
    .input-item{
        font-size: .75em;
        height: 2rem;
        width: 75%;
    }
    .span-input{
        font-size: .85em;
    }
    .contactanos-text{
        text-align: center;
    }
    .table img{
        width: 10%;
    }
    .vehiculo-columna, .precio-columna{
        font-size: 1.1em;
    }
    .cabecera-table{
        font-size: .75rem;
    }
    .leaflet-container{
        border-radius: 20px;
    }
    .container-map{
        width: 90%;
        height: 300px;
        z-index: -99;
        margin-top: -60px;
    }
}
@media (max-width: 500px){
    .section-nosotros-left-caracteristica-detalle{
        margin: .5rem;
    }
    .section-nosotros-left-caracteristica{
        font-size: 1em;
        min-width: 100%;
        padding: 1rem 0;
    }
    .servicios-title, .contactanos-title, .section-tarifas-title{
        font-size: 1.3em;
        width: 80%;
    }
    .section-nosotros-left-quienes-somos{
        width: 100%;
        font-size: 1.25em;
    }
    .servicio{
        font-size: .6em;
        margin: 1.5rem 0;
        height: 400px;
    }
    .servicio-text{
        font-size: 1.1em;
        text-align: justify;
    }
    .servicio img{
        width: 80%;
    }
    .caracteristica-img{
        width: 30%;
    }
    .input-item{
        width: 60%;
    }
    .vehiculo-columna, .precio-columna{
        font-size: .65em;
    }
    .contactanos-text{
        font-size: .75em;
    }
}


