.carousel-principal{
    width: 95%;
    padding: .5rem;
    background: white;
    box-sizing: border-box;
    margin: 0 auto;
}
.img-principal-carousel{
    height: 35rem;
    width: 100%;
    /* box-sizing: border-box; */
    /* object-fit: cover; */
}
.slickk-arrow{
    color: white;
}

@media (max-width: 950px){
    .img-principal-carousel{
        height: 15rem;

    }
    .carousel-principal{
        width: 85%;
    }
}

